@use './mixin' as *;
@use './setting' as *;


.sleep{
  position: relative;
height: 100vh;
width: 100vw;
  &__load{
    position: absolute;
    top: 50%;
    left: 30%;
    transform: translate(-50%,-50%);
    display: flex;
  }

  &__hituzi{
    height: 200px;
    width: 400px;
    position: relative;
    animation: hituzi 3s forwards , hituzi2 2s 2s forwards;

    &::before , &::after{
      content: "";
      position: absolute;
      display: block;
      width: 10px;
      height: 5px;
      background-color: #333;
    }

    &::before{
      top: 30px;
      left: 30px;
    }
  }

  @keyframes hituzi {
    0%{
      transform: translateX(0) translateY(0);
    }33%{   
transform: translateX(300px) translateY(-300px);
}50%{
      transform:translateX(600px)translateY(0);
    }100%{
      transform: translateX(600px)translateY(0)  scale(-1, 1);
    }
  }

  @keyframes hituzi2 {
    0%{
      transform: translateX(600px)translateY(0)  scale(-1, 1);
    }33%{
      transform: translateX(300px) translateY(-300px);

    }50%{
      transform: translateX(0) translateY(0);
      }80%{
        transform: translateX(300px) translateY(-400px) rotate(360deg);
      }100%{
        transform: translateX(600px) translateY(0);
      }
    }

  &__saku{
    height: 300px;
    width: 300px;
  }
}

.sheep{
  height: 200px;
  width: 350px;
  position: relative;

  animation: hituzi 5s infinite , hituzi2 5s 2s infinite;

  &::before{
    content: "";
    position: absolute;
    display: block;
    top: 65%;
    right: -22px;
    // padding: 16px;
    font-size: 10px;
    height: 16px;
    width: 24px;
    background-color: crimson;
  }

  &:after{
    content: "";
    position: absolute;
    display: block;
    top: 68%;
    right: -22px;
    // padding: 16px;
    font-size: 10px;
    height: 2px;
    width: 20px;
    background-color: brown;
  }

  &__head{
    height: 60px;
    width: 100px;
    background-color: #ffe6ce;
    position: absolute;
    top: 50%;
    right: 0;

    &::before{
      content: "◉";
      position: absolute;
      display: block;
      top: 0;
      right: 0;
      padding: 16px;
      font-size: 10px;
    }

    &::after{
      content: "◉";
      position: absolute;
      display: block;
      top: -10px;
      right: 0;
      padding: 16px;
      font-size: 10px;
    }
  }

  &__body{
    height: 100px;
    width: 250px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    right: 25%;
    background-image: url(../img/body.png);
    background-position: center;
    background-size: cover;
  }

  &__reg{
    width: 100px;
    height: 100px;
    position: absolute;
    bottom: -96px;
    left: 25%;

    >img{
      height: 30px;
      width: 100%;
    }
  }
}

