  0%{
    transform: translateX(0) translateY(0);
  }33%{   
transform: translateX(300px) translateY(-300px);
}50%{
    transform:translateX(600px)translateY(0);
  }100%{
    transform: translateX(600px)translateY(0)  scale(-1, 1);
  }
